body {
  font-family: 'Poppins', sans-serif;
}

html {
  background-color:cornsilk
}

div {
  border-radius: 20px;
}
h4 {
  color: gray;
}
input {
  border: none;
  width: 30%;
  height: 35px;
}

.viewbtn {
  border: none;
  border-radius: 5px;
  background-color: rgb(218, 177, 149);
  text-align: center;
  font-size: 15px;
  padding: 10px 15px;
  transition-duration: 0.3s;
}

.viewbtn:hover {
  cursor: pointer;
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
  background-color: rgb(54, 36, 9);
}

.deletebtn {
  margin-bottom: 250px;
  margin-left: 555px;
  position: absolute;
  color: red;
}

.deletebtn:hover {
  cursor: pointer;
}

.likebtn {
  border: none;
  background: none;
}

.likebtn:hover {
  cursor: pointer;
}

.header {
  text-align: center;
  color: darkblue;
}

.header2 {
  text-align: center;
}

.blogform {
  text-align: center;
  border: 2px solid;
}

.blogformprop {
  justify-content:center;
  align-items: center;
  display: flex;
  flex-direction: column;
  padding-bottom: 10px;
}

.blogformprop input {
  margin: 5px 5px;
}

.blogs {
  border: 2px solid;
  margin-top: 10px;

}

.singleblog {
  border: 2px dashed;
  border-radius: 10px;
  display: inline-flex;
  flex-direction: column;
  align-items:center;
  justify-content: center;
  padding: 7px 13px;
  margin: 10px 25px;
  width: 630px;
  height: 300px;
}

a button {
  text-decoration: none;
  color: white;
}

a button:hover {
  color: rgb(218, 177, 149);
}